html {
  --color-blue: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav {
  align-items: center;
  display: flex;
}

.nav-list-item {
  margin-left: 20px;
}

.nav-list-item:first-child {
  margin-left: 0;
}

.link-item {
  color: inherit;
  height: 60px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.link-item:hover {
  color: var(--color-blue);
}

.is-active {
  color: var(--color-blue);
  position: relative;
}

.is-active:after {
  content: "";
  background: var(--color-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.img-logo {
  height: 40px;
}

